.video {
  width: 364px;
  height: 228px;

  @include vp-1199 {
    min-width: 266px;
    width: 39%;
    height: 167px;
    max-height: 100%;
    padding-bottom: 24%;
  }

  @include vp-767 {
    width: 100%;
    padding-bottom: 52.2%;
  }

  @include hover-focus {
    & .video__button {
      transform: translate(-50%, -50%) scale(1.1);
    }
  }
}

.video__overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: $color-dark-cerulean-opacity;
  z-index: 1;
}

.video__cover {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;

  @include vp-767 {
    object-position: top;
  }
}

