.video__button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
  display: block;
  width: 100px;
  height: 100px;
  padding: 0;
  background-color: transparent;
  background-image: url("../img/svg/video-button.svg");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  transition: transform $trans-default;

  @include vp-1199 {
    width: 73px;
    height: 73px;
  }

  @include hover-focus {
    transform: translate(-50%, -50%) scale(1.1);
  }
}
